.container{
    width: 100%;
    height: max-content;
    display: flex;
    align-items: center;
    justify-content: center;
}
.wrapper{
    width: 100%;
    position: relative;
    display: flex;
    flex-direction: column;
}
.hiddenImageCartel{
    position: absolute;
    width: 30%;
    margin-bottom: 3.8vw;
    left: 2.7vw;
    z-index: 5;
    transition: all .3s;
    opacity: 0;
    visibility: hidden;
}
.hiddenImageCartelActive{
    left: 2.7vw;
    transition: all .3s;
    width: 94vw;
    position: absolute;
    margin-bottom: 3.8vw;
    z-index: 5;
}
.hiddenImageTriada{
    position: absolute;
    width: 50%;
    bottom: 3.5vw;
    left: 2.7vw;
    z-index: 5;
    transition: all .3s;
    opacity: 0;
    visibility: hidden;
}
.hiddenImageTriadaActive{
    left: 2.7vw;
    bottom: 3.3vw;
    transition: all .3s;
    width: 94vw;
    position: absolute;
    z-index: 5;
}
.hiddenImageRussians{
    position: absolute;
    width: 50%;
    margin-bottom: 3.8vw;
    right: 2.7vw;
    z-index: 5;
    transition: all .3s;
    opacity: 0;
    visibility: hidden;
}
.hiddenImageRussiansActive{
    right: 2.7vw;
    transition: all .3s;
    width: 94vw;
    position: absolute;
    margin-bottom: 3.8vw;
    z-index: 5;
}
.hiddenImageCosanostra{
    position: absolute;
    width: 50%;
    bottom: 3.5vw;
    right: 2.7vw;
    z-index: 5;
    transition: all .3s;
    opacity: 0;
    visibility: hidden;
}
.hiddenImageCosanostraActive{
    bottom: 3.5vw;
    right: 2.7vw;
    transition: all .3s;
    width: 94vw;
    position: absolute;
    z-index: 5;
}
@media (min-width: 1201px) {
    .container{
        display: none;
    }
}